<template>
  <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
            <strong v-show="schema.required"><span style="color:red;"> * </span></strong>
        </label>
      <input type="checkbox" class="col-5 form-control" :id="schema.id" v-model="value">
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue (value) {
        return this.value
      }
    },
    data () {
      return {
        value: ''
      }
    }
  }
</script>



