<template>
    <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
        </label>
        <div>
            <a :href="href" target="_blank" rel="noopener noreferrer"><span>{{value}}</span></a>
        </div>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue () {
        return this.href
      },
      changeValue (newValue) {
        this.value = newValue
      },
      getDisplayText () {
        if (this.schema.value !== '') {
          return this.schema.value
        } else {
          return this.schema.href
        }
      }
    },
    data () {
      return {
        href: this.schema.href,
        value: this.getDisplayText()
      }
    }
  }
</script>



