<template>
    <div class="form-group">
        <div class="form-inline">
            <label class="col-5">
                <span v-text="schema.label"></span>
                <strong v-show="schema.required"><span style="color:red;"> * </span></strong>
            </label>
            <div class="radio" v-for="item in schema.options">
                <label class="form-check-label col-12">
                    <input type="checkbox" :value="item.value" v-model="value">
                    {{item.label}}
                </label>
            </div>
        </div>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue () {
        return this.value
      },
      changeValue (newValue) {
        this.value = newValue
      },
      addOption () {
        console.log(this.schema)
      }
    },
    data () {
      return {
        value: []
      }
    }
  }
</script>



