<template>
  <div class="myform">
   <fieldset v-if="formUI">
     <template v-for='row in formUI' form-group>
        <div class= "form-group" v-for='field in row'>
            <!-- TODO: submit-click event must be set only for submit button -->
          <component :is='getFieldType(field)'
            :model='getModel(field)'
            :schema='field'
            :uiSchema='getUISchema(field)'
            @submit-click="_onSubmitClick"
            >

          </component>

        </div>
     </template>
  </fieldset>

  </div>
</template>

<script>
import {each} from 'lodash'

let fieldComponents = {}
let coreFields = require.context('@/components/core/fields', false, /^\.\/([\w-_]+)+Field.vue$/)
console.log('yyyyyyyyyyyyyyyy', coreFields.keys())
each(coreFields.keys(), (key) => {
  let compName = key.replace(/^\.\//, '').replace(/\.vue/, '')
  fieldComponents[compName] = coreFields(key).default
})
console.log('xxxxxxxxx', fieldComponents)

export default {
  components: fieldComponents,
  name: 'Formu',
  data () {
    return {
      msg: 'myForm.js form rendering framework',
      formUI: this.$root.formUI,
      data: {}
    }
  },
  methods: {
    _onSubmitClick () {
      console.log('asdfasfasfasfasfasfasfasfasfas')
      this.$root._onSubmit(this.getData())
    },
    getFieldType (schema) {
      return schema.type + '-field'
    },
    getModel (field) {
      return 'field-text'
    },
    getField (field) {
      if (typeof field === 'string') {
        return this.$children.find(item => item.getName() === field)
      }
    },
    getAllFields () {
      return this.$children.slice(0)
    },
    getValue (field) {
      return this.$children[field].getValue()
    },
    getUISchema (field) {
      return 'field-text'
    },
    getData () {
      let fields = this.getAllFields()
      let data = {}

      for (let field of fields) {
        let fieldName = field.getName()

        if (fieldName) {
          data[fieldName] = field.getValue()
          data[fieldName + '_label'] = field.getValue()
        }
      }

      return data
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



