<template>
    <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
        </label>
        <div class="col-7">
            <img class="img-fluid img-thumbnail" :src="schema.src" :alt="schema.alternateText">
        </div>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue () {
        return this.value
      }
    }
  }
</script>



