<template>
  <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
            <strong v-show="schema.required"><span style="color:red;"> * </span></strong>
        </label>
        <select v-model="selected">
            <option  v-for="item in schema.options" :value="item.value"> {{item.label}}</option>

        </select>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue () {
        return this.selected
      }
    },
    data () {
      return {
        selected: this.schema.data.value || this.schema.defaultValue

      }
    }
  }
</script>



