<template>
    <div>
        <h1 v-text="schema.label"></h1>
        <hr>
    </div>
</template>

<script>
import fieldMixin from '../../../mixins/field'

export default {
  mixins: [fieldMixin],
  props: [
    'model',
    'schema'
  ],
  methods: {
    getValue: function () {
    },
    setValue: function () {
    }
  },
  data () {
    return {
      'size': 'h3'
    }
  }
}
</script>



