<template>
    <div class="button-form" align="center">
        <button @click="onClick" type="submit" :class="'btn btn-' + color">{{ schema.label }}</button>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    data () {
      return {
        color: 'primary'
      }
    },
    methods: {
      onClick: function (e) {
        e.preventDefault()
        this.$emit('submit-click')
      }
    }
  }
</script>



