<template>
    <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
            <strong v-show="schema.required"><span style="color:red;"> * </span></strong>
        </label>
        <textarea class="col-5 form-control" :rows="schema.rows" v-model="value"></textarea>
    </div>
</template>

<script>
import fieldMixin from '../../../mixins/field'

export default {
  mixins: [fieldMixin],
  props: [
    'model',
    'schema'
  ],
  methods: {
    setValue: function () {
    }
  },
  computed: {},
  data () {
    return {
      value: this.schema.data && this.schema.data.value
    }
  }
}
</script>



