<template>
    <div class="form-group form-inline">
        <label class="col-5" align="center">
            <span v-text="schema.label"></span>
        </label>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    data () {
      return {
      }
    }
  }
</script>



