<template>
    <div class="button-form" align="center">
        <button type="button" :class="'btn btn-' + color">{{ schema.label }}</button>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      getValue () {
        return ''
      }
    },
    data () {
      return {
        color: 'default'
      }
    }
  }
</script>



