<template>
    <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
            <strong v-show="required"><span style="color:red;"> * </span></strong>
        </label>
        <input type="suggest" v-model="schema.value">
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue () {
        return this.value
      }
    },
    data () {
      return {
        required: this.schema.required
      }
    }
  }
</script>



