<template>
    <div class="form-group form-inline">
        <label class="col-5 signature-label">
            <span v-text="schema.label"></span>
        </label>
        <div class="col-12 signature-content" style="text-align: center;">
            <canvas class="signature-canvas" style="border: solid 1px;"></canvas>
            <div class="signature-controls">
                <button type="button" class="btn btn-primary signature-save">Save</button>
                <button type="button" class="btn btn-secondary signature-clear">Clear</button>
            </div>
        </div>
    </div>
</template>

<script>
  import SignaturePad from 'signature_pad'
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    data () {
      return {
        instanceSignature: null
      }
    },
    mounted () {
      let that = this
      let btnSave = this.$el.querySelector('.signature-save')
      let btnClear = this.$el.querySelector('.signature-clear')
      let canvas = this.$el.querySelector('.signature-canvas')
      this.instanceSignature = new SignaturePad(canvas)
      btnSave.addEventListener('click', function () {
        console.log(that.getValue())
      })
      btnClear.addEventListener('click', function () {
        that.clear()
      })
    },
    methods: {
      /**
       * Gets data signature on base64
       */
      getValue: function () {
        return this.instanceSignature.toDataURL('image/URL')
      },
      /**
       * Clears the signature's canvas
       */
      clear: function () {
        if (this.instanceSignature) {
          this.instanceSignature.clear()
        }
      }
    }
  }
</script>



