/**
 * Main class to render the fiends
 */
class MyForm {
  constructor (options) {
    this.el = options.el || '#app'
    this.jsonSchema = options.jsonSchema
    this.formUI = options.formUI
    this.formData = options.formData
    this.onSubmit = options.onSubmit
  }
  render () {
    let form = this.getForm(this.formUI)
    let that = this
    let vm = new Vue({
      el: this.el,
      router,
      template: '<App/>',
      components: { App },
      data: {
        model: this.model || {},
        formUI: form
      },
      methods: {
        getData () {
        },
        getValue (field) {
          return this.formUI.getValue(field)
        },
        _onSubmit (data) {
          if (typeof that.onSubmit === 'function') {
            that.onSubmit(data)
          }
        }
      }
    })
    this.vm = vm
  }
  getForm () {
    let form = new SchemaManager({
      jsonSchema: this.jsonSchema,
      formUI: this.formUI,
      formData: this.formData
    })
    return form.getFormDefinition()
  }
  getData () {
    this.vm.getData()
  }
}
/**
 * instance example
 */
let instance = new MyForm({
  model: {},
  jsonSchema: jsonSchema,
  formUI: formUI,
  el: '#app',
  onSubmit: function (data) {
    console.log(data)
  },
  models: {
    id: 1,
    title: 'That is the title',
    name: '',
    description: 'form with vue'
  }
})
// debugger
instance.render()
export { MyForm }



