<template>
    <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
        </label>
        <input type="hidden" :value="schema.defaultValue" v-model="value">
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue (value) {
        return this.value
      }
    },
    data () {
      return {
        value: ''
      }
    }
  }
</script>



