<template>
    <div class="form-group form-inline">
        <label class="col-5">
            <span v-text="schema.label"></span>
            <strong v-show="schema.required"><span style="color:red;"> * </span></strong>
        </label>
        <div class="radio" v-for="item in schema.options">
            <label class="form-check-label col-12">
                <input type="radio" :value="item.value" name="radioVue" v-model="value">
                {{item.label}}
            </label>
        </div>
    </div>
</template>

<script>
  import fieldMixin from '../../../mixins/field'

  export default {
    mixins: [fieldMixin],
    props: [
      'model',
      'schema'
    ],
    methods: {
      setValue (value) {
        this.value = value
      },
      getValue () {
        return this.value
      },
      changeValue (newValue) {
        this.value = newValue
      }
    },
    data () {
      return {
        value: ''
      }
    }
  }
</script>



